import React, { useCallback, useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useParams } from "react-router-dom";
import { Button } from "primereact/button";
import { Card } from "primereact/card";
import { InputNumber } from "primereact/inputnumber";
import { InputSwitch } from "primereact/inputswitch";
import { InputText } from "primereact/inputtext";
import { Message } from "primereact/message";
import { shallowEqual } from "react-redux";
import { useAppDispatch, useAppSelector } from "../../utilities/hooks";
import { currencyActions } from "../../store/currency/currencySlice";
import { IAppState } from "../../models/appState";
import { ICurrency } from "../../models/currency";
import { v4 as uuidv4 } from "uuid";

const CurrencyEdit: React.FC = () => {
  const defaultItem: ICurrency = {
    id: "",
    name: "",
    symbol: "",
    isO4217: "",
    conversionRate: 0,
    status: false,
    default: false,
  };
  const navigateTo = "/currencies";
  const navigate = useNavigate();
  const [subtitle, setSubtitle] = useState("Create");
  const [item, setItem] = useState(defaultItem);
  const [dirty, setDirty] = useState(false); // if form input has been changed, dirty is true
  const [valid, setValid] = useState(false); // if form input is valid, valid is true
  const [errors, setErrors] = useState({}); // indicates if fields are valid
  const params = useParams();
  const memo = useRef(true);
  const [currency, isFetching, isError, currencies] = useAppSelector(
    (state: IAppState) => [
      state.currencies.currency,
      state.currencies.isFetching,
      state.currencies.isError,
      state.currencies.currencies,
    ],
    shallowEqual
  );
  const dispatch = useAppDispatch();

  useEffect(() => {
    if (memo.current) {
      memo.current = false;
      if (params.mode === "edit") {
        setSubtitle("Edit");
        const id = params.id;
        if (id) {
          dispatch(currencyActions.getCurrencyById(id));
        }
      }
    }
  }, [params, dispatch]);

  useEffect(() => {
    if (
      params.mode === "edit" &&
      item.id !== currency?.id &&
      !isFetching &&
      !isError
    ) {
      setItem(currency);
    }
  }, [params, item, currency, isFetching, isError]);

  const isNameValid = useCallback(
    (name: string) => {
      let result = true;
      if (item.id.length === 0) {
        if (
          currencies.find((x) => x.name?.toLowerCase() === name?.toLowerCase())
        ) {
          result = false;
        }
      } else {
        if (
          currencies.find(
            (x) =>
              x.name?.toLowerCase() === name?.toLowerCase() && x.id !== item.id
          )
        ) {
          result = false;
        }
      }

      return result;
    },
    [item.id, currencies]
  );

  useEffect(() => {
    if (!dirty) {
      return;
    }

    let isValid = true;
    for (const [key, value] of Object.entries(item)) {
      if (key === "id") {
        continue;
      }

      if (typeof value === "string") {
        if (value?.trim().length === 0) {
          isValid = false;
          break;
        }
      } else if (typeof value === "number") {
        if ((value || 0) <= 0) {
          isValid = false;
          break;
        }
      }
    }

    setValid(isValid && isNameValid(item.name || ""));
  }, [dirty, item, isNameValid]);

  const onChange = (e: any) => {
    setDirty(true);
    const {
      target: { name: tname, value: tvalue },
    } = e;

    setItem({ ...item, [tname]: tvalue });
    if (tname === "name") {
      const isValidName = isNameValid(tvalue);
      setErrors({
        ...errors,
        nameInvalid: !isValidName,
        [tname]: tvalue ? tvalue.toString().trim().length === 0 : true,
      });
    } else if (tname !== "status") {
      setErrors({ ...errors, [tname]: tvalue?.trim().length === 0 });
    }
  };

  const onInputNumberChange = (e: any) => {
    setDirty(true);
    const newValue: number = e.value || 0;
    setItem({ ...item, conversionRate: newValue });
    setErrors({ ...errors, conversionRate: newValue <= 0 });
  };

  const onSaveClick = async () => {
    if (subtitle !== "Edit") {
      item.id = uuidv4();
    }

    dispatch(currencyActions.updateCurrency(item));
    setTimeout(() => {
      dispatch(currencyActions.getCurrencies());
    }, 300);
    navigate(navigateTo);
  };

  const onCancelClick = async () => {
    navigate(navigateTo);
  };

  return (
    <>
      <h3 className="caption">Currency: {subtitle}</h3>
      <div className="grid">
        <div className="col-12">
          <span className="field-required">*</span> - Required field
        </div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Currency</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="CurrencyName">
                  Currency Name <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <InputText
                  id="CurrencyName"
                  name="name"
                  maxLength={36}
                  placeholder="US Dollar"
                  value={item.name}
                  onChange={onChange}
                />
                {(errors as any)?.name && (
                  <Message severity="error" text="Currency Name is required." />
                )}
                {(errors as any)?.nameInvalid && (
                  <Message
                    severity="error"
                    text="Currency Name already exists."
                  />
                )}
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="ISO4217">
                  ISO 4217 Currency Code
                  <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <InputText
                  id="ISO4217"
                  name="isO4217"
                  maxLength={3}
                  placeholder="USD"
                  value={item.isO4217}
                  onChange={onChange}
                />
                {(errors as any)?.isO4217 && (
                  <Message
                    severity="error"
                    text="ISO 4217 Currency Code is required."
                  />
                )}
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Rate">
                  Conversion Rate <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <InputNumber
                  id="Rate"
                  name="conversionRate"
                  value={item.conversionRate}
                  maxFractionDigits={4}
                  onValueChange={(e) => onInputNumberChange(e)}
                />
                {(errors as any)?.conversionRate && (
                  <Message
                    severity="error"
                    text="Conversion Rate is required."
                  />
                )}
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="Symbol">
                  Currency Symbol <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <InputText
                  id="Symbol"
                  name="symbol"
                  maxLength={36}
                  value={item.symbol}
                  onChange={onChange}
                />
                {(errors as any)?.symbol && (
                  <Message
                    severity="error"
                    text="Currency Symbol is required."
                  />
                )}
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Status">
                  Status <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <div className="p-fixed-switch">
                  <InputSwitch
                    id="Status"
                    name="status"
                    checked={item.status || false}
                    value="0"
                    onChange={onChange}
                  />
                </div>
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col hidden-xs">&nbsp;</div>
              <div className="col hidden-xs">
                <div className="p-fixed-switch">&nbsp;</div>
              </div>
            </div>
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
        <div className="col-12">
          <Button
            icon="pi pi-check"
            className="p-button-rounded p-button-outlined"
            tooltip="Save"
            disabled={!valid}
            onClick={onSaveClick}
          />
          &nbsp; &nbsp;
          <Button
            icon="pi pi-times"
            className="p-button-rounded p-button-outlined"
            tooltip="Back to List"
            onClick={onCancelClick}
          />
        </div>
      </div>
    </>
  );
};

export default CurrencyEdit;
